import React,{Component} from "react";

import {Link,browserHistory,hashHistory} from "react-router";
import Head1 from "../../components/head1"
import {setCookie,getCookie,removeCookie} from "../../../../libs/cookie.js";
// import "../../../../libs/layer.js"
import {connect} from "react-redux"

import {} from "../../actions"
@connect(
    (state)=>({...state})
)

export default class Addetails extends Component{
    constructor(props){
        super(props)
    }
    handleExit(){
        layer.open({
            title:"",
            content: ''
            ,btn: ['', '']
            ,yes: function(index){
            removeCookie("username");
            location.reload();
            hashHistory.push('/mine')
            layer.close(index);
            }
            ,no:function(index){
                layer.close(index);
            }
          });     
    }
    
    render(){
        var isButton = this.props.params.num;
        var displayStyle = parseInt(isButton)?"block":"none";
        return(
            <div className="setting">
                <Head1 word=""/>
                <div className="setting-set">
                    <h1 className="set-head"></h1>
                    <Link className="set-common">
                        <span></span>
                        <i className="iconfont icon-gou"></i>
                    </Link>
                    <Link className="set-common">
                        <span></span>
                        <em>84.5M</em>
                    </Link>
                    <Link className="set-common2"><span></span></Link>
                    <Link className="set-common2"><span></span></Link>
                    <button type="button" className="settingButton commonButton" style={{display:displayStyle}} onClick={this.handleExit} ref="btn"></button>
                </div>
            </div>
        )
    }
}


