import React,{Component} from "react";

import {Link} from "react-router";
import Head1 from "../../components/head1"

import {connect} from "react-redux"

import {} from "../../actions"
@connect(
    (state)=>({...state})
)

export default class Suggestion extends Component{
    constructor(props){
        super(props)
        this.state={
            suggestionIcon:[
                {txt:"",icon:"icon-zan"},
                {txt:"",icon:"icon-shoucang3"},
                {txt:"",icon:"icon-guanzhu"},
                {txt:"",icon:"icon-pinglun"},
                {txt:"",icon:"icon-lingdang1"}
            ]
        }
    }
    render(){
        const {suggestionIcon} = this.state;
        return(
            <div className="suggestion">
                <Head1 word=""/>
                <div className="suggestion-nav">
                    {
                        suggestionIcon.map((item,idx)=>{
                            return(
                               <Link key={idx} className="nav-list">
                                    <section className="nav-section">
                                        <i className={"iconfont "+item.icon}></i>
                                        <span>{item.txt}</span>
                                    </section>
                                    <i className="iconfont icon-ic_send_px onlyi"></i>
                               </Link>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}


